<template>
  <div class="full-div">
    <img
        src="../../public/space.gif"
        class="full-div-img"
    >
    <div class="darkener"></div>
    <div class="main-wrapper">
      <div>
        <p v-if="getYearAlex() < 2" class="world-text">Мир по нашему летоисчеслению ещё очень молод</p>
        <p v-else-if="getYearAlex() < 5" class="world-text">Похоже, Мир уже крепко держится на ногах</p>
        <p v-else class="world-text">Мир уже чертовски стар, а мы всё ещё молоды</p>
        <p class="text-epoch">{{alexTime}}</p>
        <p class="text-epoch">{{vikTime}}</p>
      </div>
    </div>
    <footer class="footer">
      <p>Powered by Love</p>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'MainWin',
  data: function () {
    return {
      alexTime: 0,
      vikTime: 0,
    }
  },
  methods: {
    getUTC(){
      let date = new Date();
      let yearUTC = date.getUTCFullYear();
      let monthUTC = date.getUTCMonth(); // start with 0
      monthUTC = monthUTC < 10 ? '0' + (monthUTC + 1) : monthUTC;
      let dayUTC = date.getUTCDate();
      dayUTC = dayUTC < 10 ? '0' + dayUTC: dayUTC;
      let hoursUTC = date.getUTCHours();
      hoursUTC = hoursUTC < 10 ? '0' + hoursUTC: hoursUTC;
      let minutesUTC = date.getUTCMinutes();
      minutesUTC = minutesUTC < 10 ? '0' + minutesUTC : minutesUTC;
      let secondsUTC = date.getUTCSeconds();
      secondsUTC = secondsUTC < 10 ? '0' + secondsUTC: secondsUTC;
      console.log(`${yearUTC}-${monthUTC}-${dayUTC}T${hoursUTC}:${minutesUTC}:${secondsUTC}Z`);
      return new Date(`${yearUTC}-${monthUTC}-${dayUTC}T${hoursUTC}:${minutesUTC}:${secondsUTC}Z`);
    },
    getCurrentUnixUTC(){
      return Math.floor((new Date()).getTime() / 1000);
    },
    getUnixUTCVik(){
      // return Math.floor((new Date('2020-02-03T21:00:00Z')).getTime() / 1000);
      return Math.floor((new Date('2020-02-04T21:00:00Z')).getTime() / 1000);
    },
    getUnixUTCAlex(){
      return Math.floor((new Date('2020-02-27T21:00:00Z')).getTime() / 1000);
    },
    getTimeVik(){
      let diff = this.getCurrentUnixUTC() - this.getUnixUTCVik();
      let days = (((diff - diff % (86400)) / (86400)) + 1) % 365;
      let hours = ((diff - diff % (3600)) / 3600) % 24;
      let mins = (diff - diff % (60)) / (60) % 60;
      let seconds = diff - (diff - (diff % 60));

      let year = Math.floor((((diff - diff % (86400)) / (86400)) + 1) / 365);
      return `${year} год Викторианской Эры,  ${days} д. ${hours} ч. ${mins} мин. ${seconds} сек.`;
    },
    getTimeAlex(){
      let diff = this.getCurrentUnixUTC() - this.getUnixUTCAlex();
      let days = ((diff - diff % (86400)) / (86400)) + 1;
      let hours = ((diff - diff % (3600)) / 3600) % 24;
      let mins = (diff - diff % (60)) / (60) % 60;
      let seconds = diff - (diff - (diff % 60));

      let year = Math.floor((((diff - diff % (86400)) / (86400)) + 1) / 365);
      return `${year} год Александрийской эры,  ${days} д. ${hours} ч. ${mins} мин. ${seconds} сек.`;
    },
    updateTime(){
      this.alexTime = this.getTimeAlex();
      this.vikTime = this.getTimeVik();
    },
    getYearAlex(){
      let diff = this.getCurrentUnixUTC() - this.getUnixUTCAlex();
      let days = ((diff - diff % (86400)) / (86400)) + 1;
      let year = Math.floor(days / 365);
      return year;
    }

  },
  created() {
    this.timer = setInterval(this.updateTime, 100)
  }
}
</script>

